<template>
  <div id="app">
    <my-button @click="random">Random</my-button>
    <my-button type="submit" @click.prevent="addSeries" >Add type</my-button>
    <my-select
    :model-value="selected"
    @update:model-value="setSelect"
    :options="chartOption" 
    ></my-select>
    <p v-if="selected"> You chose {{selected}} </p>
    <chart />
  </div>
</template>

<script>
import Chart from './components/Chart.vue'
import {mapState, mapMutations, mapActions} from 'vuex';

export default {
  name: 'App',
  components: {
   Chart
  },
   methods:{
        ...mapMutations({
            setSelect:'setSelect',
            setChart:'setChart'
        }),
         ...mapActions({
            updateSeries:'updateSeries',
            addRandom:'addRandom',
        }),
        addSeries() {

         for(let i = 0; i <= this.chartOption.length; i++){
            if(this.selected == this.chartOption[i].name){
              this.updateSeries(this.chartOption[i]);
            } 
         }
        },
        random: function () {
           for(let i = 0; i <= this.chartOption.length; i++){
            this.addRandom(this.chartOption[i].data.map((num) => Math.floor(Math.random() * 5000)));
         }
            // console.log(this.chartOption[0].data.map((num) => Math.floor(Math.random() * 5000)))
				},
   },
   
  computed:{
    ...mapState({
      addOptions:state => state.chartOptions.series,
      selected:state => state.selected,
      isSubmitted:state => state.isSubmitted,
      chartOption:state => state.select,
      submited:state => state.isSubmitted,
      }),
    },
}
</script>

<style>
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
</style>
