import { createStore } from 'vuex'

export default createStore({
  state: { 
    selected:'',
    // isSubmitted: false,
    select:[
      {name: 'Physics',data: [3206,  3021, 3467, 3166]}, 
      {name: 'Chemistry',data: [2263, 1223, 1729, 1628]}
    ],
    chartOptions: {
        chart: {
        type: 'area'
        },
        title: {
          text: 'development by numbers'
        },
        xAxis: {
          categories: ['2021', '2022', '2023', '2024'],
          tickmarkPlacement: 'on',
          title: {
            enabled: false
          }
        },
        yAxis: {
          title: {
            text: 't/m2'
          },
          labels: {
            formatter: function () {
              return this.value / 1000;
            }
          }
        },
        tooltip: {
          split: true,
          valueSuffix: ' millions'
        },
        plotOptions: {
          area: {
            stacking: 'normal',
            lineColor: '#666666',
            lineWidth: 1,
            marker: {
              lineWidth: 1,
              lineColor: '#666666'
            }
          }
        },
        series: [
          {name: 'Mathematics',data: [5002, 4835, 5234, 4868]}, 
        ]
    }
  },
  getters: {},

  mutations: {
    setSelect(state, selected){
      state.selected = selected;
    },
    setChart(state,payload){
      if(state.chartOptions.series.length <= 2){
        state.chartOptions.series.push(payload);
      }
     
    },
    setRandom(state,payload){
      for(let i = 0; i <= state.chartOptions.series.length; i++){
        state.chartOptions.series[i].data= payload;
     }
    },
  },
  actions: {
    updateSeries(context, status) {
      context.commit("setChart", status);
    },
    addRandom(context, status) {
      context.commit("setRandom", status);
    }
  },
})
