<template>
    <select :value="modelValue" @click="changeOption">
        <option disabled value=''>Mathematics</option>
        <option
        v-for="option in options"
        :key="option.value"
        :value="option.value">
            {{option.name}}
        </option>
    </select>
</template>

<script>
export default {
    name:'my-select',
    props:{
        modelValue:{
            type:String
        },
        options:{
            type:Array,
            default:() => []
        }
    },
    methods:{
        changeOption(event){
            this.$emit('update:modelValue',event.target.value);
        },
    }
}
</script>
<style module>
    select{
        width: 130px;
        height: 30px;
        border: 1px solid #FF3F4D;
        border: 1px solid rgba(224,211,125,1);
        margin: 0 20px;
        box-shadow: 0 2px 6px rgba(100, 100, 100, 0.9), 0 2px 9px rgba(100, 100, 100, 0.9);
    }
</style>